import { cookieWrite } from './cookie-write.js';

/**
 * @description
 * Removes a cookie by setting its value to an empty string and setting the expiration date to the past.
 *
 * @example
 * ``` ts
 * cookieWrite('foo', 'abc');
 * cookieRemove('foo');
 * cookieRead('foo');
 * // => null
 * ```
 *
 * @param name - The name of the cookie to remove.
 *
 * @docsCategory preference/utils/cookie
 * @codeFilter cookie
 */
export const cookieRemove = (name: string): void => {
  cookieWrite(name, '', Date.now() - 86400000);
};
